"use client";

import Image from "next/image";
import { motion } from "framer-motion";
import Link from "next/link";

export default function PesanJanji() {
  // Animasi sederhana
  const fadeIn = {
    hidden: { opacity: 0, y: 40 },
    visible: { opacity: 1, y: 0, transition: { duration: 0.8 } },
  };

  return (
    <div className="bg-[#f0f7ff] min-h-screen w-full font-sans flex flex-col">
      <motion.header
        className="py-6 px-4 max-w-4xl mx-auto flex justify-between items-center w-full"
        initial="hidden"
        animate="visible"
        variants={fadeIn}
      >
        <div className="flex items-center space-x-3">
          <Image src="/Gemini_Generated_Image_i6nyp3i6nyp3i6ny-removebg-preview.png" alt="Logo Medlitik" width={40} height={40} />
          <span className="text-2xl font-bold text-[#1a2a3a]">Medlitik</span>
        </div>
        <nav className="flex items-center space-x-8">
          <Link href="/" className="text-gray-700 hover:text-[#3570ff] font-medium">Beranda</Link>
          <span className="text-[#3570ff] font-bold">Pesan Janji</span>
        </nav>
      </motion.header>
      <main className="flex-1 flex flex-col items-center justify-center px-4 py-12">
        <motion.div
          className="bg-white rounded-2xl shadow-xl p-8 max-w-lg w-full border border-[#e3eefd]"
          initial="hidden"
          animate="visible"
          variants={fadeIn}
        >
          <h1 className="text-3xl font-bold text-[#3570ff] mb-6 text-center">Pesan Janji</h1>
          <form className="space-y-5">
            <div>
              <label className="block text-gray-700 font-medium mb-1">Nama Lengkap</label>
              <input type="text" className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]" placeholder="Nama Anda" required />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-1">Email</label>
              <input type="email" className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]" placeholder="Email" required />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-1">Nomor Telepon</label>
              <input type="tel" className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]" placeholder="08xxxxxxxxxx" required />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-1">Tanggal Janji</label>
              <input type="date" className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]" required />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-1">Pilih Dokter</label>
              <select className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]">
                <option>Dr. John Stone</option>
                <option>Dr. Bella Adams</option>
                <option>Dr. Paulin</option>
                <option>Dr. Smith</option>
              </select>
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-1">Pesan (Opsional)</label>
              <textarea className="w-full px-4 py-2 rounded border border-[#e3eefd] focus:outline-none focus:ring-2 focus:ring-[#3570ff]" rows={3} placeholder="Tulis pesan..."></textarea>
            </div>
            <motion.button
              type="submit"
              className="w-full bg-[#3570ff] text-white py-3 rounded-full font-bold hover:bg-[#2856b6] transition shadow-md mt-2"
              whileHover={{ scale: 1.03 }}
              whileTap={{ scale: 0.97 }}
            >
              Kirim Permintaan
            </motion.button>
          </form>
        </motion.div>
      </main>
    </div>
  );
}
